.projects__filter {
  display: flex;
  justify-content: center;
  margin: 4rem 0 2rem;
}

.projects__intro {
  justify-content: center;
  align-items: center;
  text-align: center;
}

.projects__filter-button {
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 0.5rem;
  /* background blur */
  color: white;
  border: 0.5px solid rgba(255, 255, 255, 0.3);
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.projects__card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.projects-item {
  width: 270px;
  height: 350px;
  flex-direction: column;
  margin: 2rem;
  border-radius: 0.5rem;
  color: #000;
  cursor: pointer;
  transition: all 0.3s ease;
  /* background blur */
  border: 0.5px solid rgba(255, 255, 255, 0.3);
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.projects-item:hover {
  border: 0.5px solid var(--blue-color);
}

.projects-img {
  width: 100%;
  height: 100%;
  position: relative;
}

.projects-img img {
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
  object-fit: cover;
}

.projects-content {
  padding: 0.5rem;
  width: 100%;
  position: relative;
  flex-direction: column;
  margin: 1rem;
}

.projects-content h4 {
  margin: 0.5rem;
  padding: 1.5;
  color: white;
}

.projects-hover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 0.5rem;
  opacity: 0;
  transition: all 0.3s ease;
}

.projects-hover div {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  margin: 1rem;
  font-family: var(--font-base);
  font-weight: 800;
  cursor: pointer;
  transition: all 0.3s ease;
}

.projects-hover svg {
  width: 50%;
  height: 50%;
  color: var(--white-color);
}

.item-active {
  background-color: var(--blue-color);
  color: #fff;
}

/* MEDIA QUERIES */

@media screen and (min-width: 2000px) {
}

/* MOBILE */
@media screen and (max-width: 450px) {
  .project-item {
    width: 100%;
    margin: 1rem;
  }
}
